<script>
import { defineComponent } from 'vue'
import FullCalendar from '@fullcalendar/vue3'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import listPlugin from '@fullcalendar/list'
import axios from 'axios';

export default defineComponent({
  components: {
    FullCalendar,
  },

  data () {
    return {
      selectedProject: [], // 선택된 프로젝트
      currentProject: [], // db에서 가져온 project list를 가공해서 저장할 배열
      currentTodo: [], // db - todo list
      isTodoVisible: false, // project 아래 todo list dropdown
      key: '',
      calendarOptions: {
        plugins: [
          dayGridPlugin,
          timeGridPlugin,
          interactionPlugin,
          listPlugin
        ],
        headerToolbar: {
          left: 'prev,next today',
          center: 'title',
          right: 'dayGridMonth,timeGridWeek,timeGridDay,listWeek'
        },
        initialView: 'dayGridMonth',
        events: [], // calendar에 뿌려줄 배열
        editable: false, // 일정 드래그 방지
        selectable: true,
        selectMirror: true,
        dayMaxEvents: true,
        weekends: true,
        select: this.handleDateSelect,
        eventClick: this.handleEventClick,
        eventsSet: this.setEvents,
      },
    }
  },

  mounted() {
    this.checkInput();

    // ////////////css -- sun: FullCalendar initialization code
    // this.$refs.calendarRef = new FullCalendar.Calendar(this.$refs.calendar, {
    //    //Calendar configuration options
    // });
    // this.$refs.calendarRef.render();
  },

  methods: {
    checkInput(){ // t_key로 login 판별
      console.log('checkInput');
      if(document.getElementsByName('t_key').length > 0  && document.getElementsByName('t_key')[0].value){
        console.log(document.getElementsByName('t_key')[0].value);
        this.getTodo(document.getElementsByName('t_key')[0].value);
      }else{
        console.log('not found input');
      }
      // this.forTest(this.key);
    },
    // forTest(temp){ // 개발할 때 임시로 사용
    //   console.log("key : " + temp);
    //   this.key = temp;
    //   this.projectsList();
    // },
    getTodo(value){
      this.key = value;
      this.projectsList();

    },
    projectsList: function() {
      var dataList = this.currentProject
      axios.get("http://localhost:8056/api/projectList?t_key="+this.key)
      .then(
        (res) => {
          console.log(">>>>>>> axios success", res.data)
          res.data.map(function(element) {
            // 날짜 데이터 - 시간 제거
            const startDate = new Date(element.start_date);
            startDate.setHours(0, 0, 0, 0);
            const endDate = new Date(element.due_date);
            endDate.setHours(0, 0, 0, 0);
            startDate.setDate(startDate.getDate() + 1);
            endDate.setDate(endDate.getDate() + 1);

            dataList.push({ // dataList에 mapping 후 저장 - currentProject
              id: element.project_num,
              title: element.project_title,
              start: startDate.toISOString().split('T')[0],
              end: endDate.toISOString().split('T')[0],
              pm: element.project_manager,
              member_num: element.member_num 
            })
          })
        },
        console.log("currentProject >>>>>>>", this.currentProject)
      ).catch(
        (err) => {
          console.log(err)
        }
      )
    },
    todoList(selectedProject) {
      var dataList = this.currentTodo
      this.selectedProject = selectedProject; // Project 클릭 시, currentProject에 선택한 project list 대입
      console.log("selected project >>>>>>>", selectedProject)
      // 선택된 project의 todo list를 axios를 통해 가져오기
      // mapping 할 때 project_num 을 id로 받았기 때문에 요청 시 selectedProject.id로 요청
      axios.post("http://localhost:8056/api/getAllTodoList", {
        project_num: selectedProject.id,
        t_key: this.key
      })
        .then((res) => {
          res.data.map(function(element) {
            console.log("elemnet >>>>>>>>", element)
            // 날짜 데이터 - 시간 제거
            const startDate = new Date(element.start_date);
            startDate.setHours(0, 0, 0, 0);
            const endDate = new Date(element.due_date);
            endDate.setHours(0, 0, 0, 0);
            startDate.setDate(startDate.getDate() + 1);
            endDate.setDate(endDate.getDate() + 1);

            dataList.push({ // dataList에 mapping 후 저장 - currentTodo
              id: element.todo_num,
              title: element.todo_title,
              start: startDate.toISOString().split('T')[0],
              end: endDate.toISOString().split('T')[0],
              member_name: element.member_name,
              content: element.content,
              member_num: element.member_num 
            })
          })
        },
        console.log("currentTodo >>>>>>>", this.currentTodo),
        )
        .catch((err) => {
          console.log(err);
        });
    },
    toggleTodo(selectedProject) {
      console.log("토글토글")
      var dataList = this.currentTodo;
      this.isTodoVisible = !this.isTodoVisible;

       // 선택된 project의 todo list를 axios를 통해 다시 불러오기
      if (!this.isTodoVisible) {
        axios.post("http://localhost:8056/api/getAllTodoList", {
          project_num: selectedProject.id,
          t_key: this.key
        })
          .then((res) => {
            res.data.map(function(element) {
              // 날짜 데이터 - 시간 제거
              const startDate = new Date(element.start_date);
              startDate.setHours(0, 0, 0, 0);
              const endDate = new Date(element.due_date);
              endDate.setHours(0, 0, 0, 0);
              startDate.setDate(startDate.getDate() + 1);
              endDate.setDate(endDate.getDate() + 1);

              dataList.push({ // dataList에 mapping 후 저장 - currentTodo
                id: element.todo_num,
                title: element.todo_title,
                start: startDate.toISOString().split('T')[0],
                end: endDate.toISOString().split('T')[0],
                member_name: element.member_name,
                content: element.content,
                member_num: element.member_num 
              })
            })
          })
          .catch((err) => {
            console.log(err);
          });
        } else {
        this.currentTodo = []; // 숨길 때 currentTodo 비우기
      }

    },
    setEvents() { // 가공된 currentProject와 calendar에 뿌려줄 todo 연결
      // this.calendarOptions.events = this.currentProject;
      this.calendarOptions.events = this.currentTodo;
    },
    handleWeekendsToggle() {
      this.calendarOptions.weekends = !this.calendarOptions.weekends // update a property
    },
    handleDateSelect(selectInfo) {
      let title = prompt('일정 제목을 입력하세요')
      let calendarApi = selectInfo.view.calendar

      calendarApi.unselect() // clear date selection
      
      if (title) {
        calendarApi.addEvent({
          id: selectInfo.id,
          title,
          start: selectInfo.startStr,
          end: selectInfo.endStr,
          allDay: selectInfo.allDay
        })
      }
    },
    handleEventClick(clickInfo) {
      if (confirm(`'${clickInfo.event.title}' 일정을 삭제하시겠습니까?`)) {
        clickInfo.event.remove()
      }
    },
  },
})
</script>

<template>
  <div class='app'>
    <div class='app-sidebar'>
      <div class='app-sidebar-section'>
        <div class='sidebar-header'>프로젝트</div>
      </div>
      <div class='app-sidebar-section'>
        <label>
          <input
            type='checkbox'
            :checked='calendarOptions.weekends'
            @change='handleWeekendsToggle'
          />
          주말 포함
        </label>
      </div>
      <div class='app-sidebar-section'>
        <h2>프로젝트 관리 ({{ currentProject.length }})</h2>
        <ul>
          <li v-for='project in currentProject' :key='project.id' @click='todoList(project); toggleTodo(project);'>
            <span>{{ project.title }}</span>
              <div class="todo-list" v-if="isTodoVisible">
                <ul>
                  <li v-for="todo in currentTodo" :key="todo.id">
                    {{ todo.title }}
                  </li>
                </ul>
              </div>
          </li>
        </ul>
      </div>
    </div>
    <div class='app-main'>
      <FullCalendar
        class='app-calendar'
        :options='calendarOptions'
      >
        <template v-slot:eventContent='arg'>
        <!-- <b>{{ arg.timeText }}</b> -->
          <span>{{ arg.event.title }}</span>
        </template>
      </FullCalendar>
    </div>
  </div>
</template>

<style>
  @import './components/calendar.css';
</style>